import React from 'react';
import './planetDetail.css'

const Planet = (props) => {

       if (props.detail === undefined) {
              return <div> No result Found </div>

       } return <div className='container-fluid text-center'>
              <h1 className='planetInfo'>Planet Info</h1>
              <table id="planets">
                     <tbody>
                            <tr>
                                   <th>Planet Name</th>
                                   <td>{props.detail.name}</td>
                            </tr>
                            <tr>
                                   <th>Climate</th>
                                   <td>{props.detail.climate}</td>
                            </tr>
                            <tr>
                                   <th>Rotation Period</th>
                                   <td>{props.detail.rotation_period}</td>
                            </tr>
                            <tr>
                                   <th>Diameter</th>
                                   <td>{props.detail.diameter}</td>
                            </tr>
                            <tr>
                                   <th>Gravity</th>
                                   <td>{props.detail.gravity}</td>
                            </tr>
                            <tr>
                                   <th>Terrain</th>
                                   <td>{props.detail.terrain}</td>
                            </tr>
                            <tr>
                                   <th>Surface Water</th>
                                   <td>{props.detail.surface_water}</td>
                            </tr>
                            <tr>
                                   <th>Population</th>
                                   <td>{props.detail.population}</td>
                            </tr>
                     </tbody>
              </table>
       </div>
}
export default Planet
