import React, { Component } from 'react';
import './login.css'
import axios from 'axios'
import SearchBar from './searchPlanet'


class Login extends Component {

  state = {

    clientUser: '',
    clientPass: '',
    backendUser: '',
    backendPass: '',
    loginSuccess: false,
    loginFailure: false

  }


  OnSubmitClick = async () => {
    const UClient = this.state.clientUser

    await axios.get(`https://swapi.dev/api/people/?search=${UClient}`)
      .then(result => this.setState({
        backendUser: result.data.results.map((people) => people.name),
        backendPass: result.data.results.map((people) => people.birth_year)
      }))
      .catch(err => console.error(err))

    const CheckAuthentication = async () => {
      const UClient = this.state.clientUser
      const UBackend = this.state.backendUser[0]
      const PBackend = this.state.backendPass[0]
      const PClient = this.state.clientPass

      if (PBackend === PClient && UClient === UBackend) {
        console.log('Successfully Login')
        this.setState({
          loginSuccess: 'Successfully Login', loginFailure: false
        })
      } else {
        console.log('Invalid Username or Password')
        this.setState({ loginFailure: 'Invalid Username or Password' })
      }
    }
    CheckAuthentication()

  }


  render() {

    if (this.state.loginSuccess) {
      return <div >
        <SearchBar user={this.state.backendUser} />
      </div>
    } else {
      return <div className='container text-center shadow a5'>

        <h1 className='a1'>STAR WARS</h1>

        <div className='container text-center'>
          <form onSubmit={(e) => e.preventDefault()}>
            <table>
              <tbody>
                <tr>
                  <td>
                    <label className='a2'>User Name: </label>
                  </td>
                  <td>
                    <input
                      onChange={e => this.setState({ clientUser: e.target.value })}
                      value={this.state.UserName}
                      placeholder='Enter Username'
                      className='a3 text-center' />
                  </td>

                </tr>
                <tr>
                  <td>
                    <label className='a2'>Password: </label>
                  </td>
                  <td>
                    <input
                      onChange={(e) => this.setState({ clientPass: e.target.value })}
                      type='password'
                      value={this.state.Password}
                      placeholder='Enter Password'
                      className='a3 text-center' />
                  </td>
                </tr>
                <tr>
                  <td></td>
                  <td>
                    <button className='a4' onClick={this.OnSubmitClick}>Submit</button>
                  </td>

                </tr>
              </tbody>
            </table>
          </form>
        </div>
        <div className='a6'>{this.state.loginFailure}</div>

      </div>
    }
  }
}
export default Login;
